import { MovingTileGrid } from "./moving_grid";

function tileSetup(): Array<MovingTileGrid> {
    const header = document.getElementById("header");
    const header_container = document.getElementById("header-canvas-container");
    const h_width = header.offsetWidth;
    const h_height = header.offsetHeight;

    const footer = document.getElementById("footer");
    const footer_container = document.getElementById("footer-canvas-container");
    const f_width = footer.offsetWidth;
    const f_height = footer.offsetHeight;

    const tile_width = 32;
    const tile_height = 32;

    const h_grid = new MovingTileGrid(h_width, h_height, tile_width, tile_height, 0xFF00FF,
        0x000000, header_container);

    const f_grid = new MovingTileGrid(f_width, f_height, tile_width, tile_height, 0xFF00FF,
        0x000000, footer_container);

    h_grid.initialize();
    h_grid.start();

    f_grid.initialize();
    f_grid.start();

    return [h_grid, f_grid];

}

function main() {
    // setup initial grids
    let grids: Array<MovingTileGrid> = tileSetup();
    
    // account for window resizing
    let clock: number;
    window.onresize = function () {
        // clear out the old timer, and wait 25ms before
        // setting back up the grids
        window.clearTimeout(clock);
        clock = window.setTimeout(() => {
            grids[0].stop();
            grids[1].stop();
            
            document.getElementById("header-canvas-container").remove();
            document.getElementById("footer-canvas-container").remove();

            const h_div = document.createElement("DIV");
            h_div.id = "header-canvas-container";
            document.getElementById("header").appendChild(h_div);

            const f_div = document.createElement("DIV");
            f_div.id = "footer-canvas-container";
            document.getElementById("footer").appendChild(f_div);

            grids = tileSetup();
        }, 25);


    };
    
}

main();